@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
@import url('https://cdn.syncfusion.com/ej2/material.css');
:root{
    --primaryBright:#fff;
    --secondaryBright: #e8e8e8;
    --accentMain: #750000;
    --tint: #8c8c8c;
    --successBlue: #66a7b8;
    --failureRed: #f19898ab;
    --shadow: #383838;
    --primaryDark: #1a1b1f;
    --appBlack: #2a2a2a;
}


* {
    box-sizing: border-box;
    margin:0;
    padding:0;
    font-family: 'Lato', sans-serif;
}

.home, .team, .reports, .forms, .library {
    background-color: var(--shadow);
   
    color: var(--primaryDark);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    
}

  
