  #loading{
    display: none;
    position: relative;
    left: 44%;
    top: 50%;
    /*display:none;*/
    border: 4px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #800000; /* Blue */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin .5s linear infinite;
    padding: 0;
    margin:0;
  
  }
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
