
:root{
    --primaryBright:#fff;
    --secondaryBright: #d4b8b8;
    --accentMain: #750000;
    --tint: #8c8c8c;
    --shadow: #383838;
    --primaryDark: #1a1b1f;
    --appBlack: #2a2a2a;
    --smoke:  #2423239d;
    --cream: #cca19cc9;
}
.version{
    position: fixed;
    top:.5px;
    right:3px;
    color:var(--secondaryBright);
    font-size: 20px;
}
.flatButton{
    position: fixed;
    color:var(--primaryBright);
    background-color: var(--appBlack);
    border-width: 1px;
    border-radius: 4px;
    padding: 4px 10px;
    font-size: 15px;
    
}
.headingTitle{
    position: fixed;
    top:.5px;
    left:3px;
    color:var(--secondaryBright);
    font-size: 20px;
    font-weight: 200;
    margin-left: 16px;
    

}
marquee {
    position: relative;
    
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    
}
.menu-barX {
    position:absolute;
    left:35px;
    top:10px;
    font-size: 1.5rem;
    background: none;
    
}
.navbar {
    position: fixed;
    
    background-color: var(--shadow);
    border-color: var(--accentMain);
    border-bottom-style: ridge;
    border-bottom-width: 5px;
    border-top-style: outset;
    border-top-width: 30px;
    
    padding-top:8px;
    height: 70px;
    width:100%;
    /* min-width:570px ; */
    display:flex;
    justify-content: flex-start;
    align-items: center;
    
}
.nav-menu {
    background-color: var(--accentMain);
    width: 150px;
    height: 100vh;
    display: flex;
    margin-top: 70px;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;

    background-color: var(--shadow);
    border-right-style: inset;
    border-right-width: 5px;
    border-color: var(--accentMain);
 
    
}

.nav-menu.active {
    left:0;
    transition:200ms;
}
.nav-menu-items {
    width: 100%;
    
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0 px 8 px 16 px;
    list-style: none;
    height: 50px;
    
}

.nav-text a {
    text-decoration: none;
    color:var(--primaryBright);
    font-size: 22px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    margin-top: 30px;
    
}

.nav-text a:hover {
    background-color:var(--appBlack);
    color:var(--tint);
    font-weight: bold;
}
.nav-menu span{
    margin-left: 16px;
}



.navbar-toggle {
    
    width: 35px;
    height:35px;
    display: flex;
    margin-top:5px;
    margin-left: 20%;
    color: var(--secondaryBright);
    background-color: var(--appBlack);
    border-radius: 25%;
    border-color: var(--tint);
    border-width: 1px;
    border-top-style: outset;
    border-left-style: outset;
}


.MainScroll{
    position:fixed;
    margin-top: 70px;
    height: 90px;
    width:100%;
    /* background-color: var(--smoke); */
    

}
.message{
    
    
    padding:5px;
    height: 50px;
    
    /* margin-right: 200px; */
    color: var(--primaryBright);
    /* border: 1px solid var(--primaryBright); */
}
.message-case{
    
    border: 1px solid var(--primaryBright);
    border-radius:8px;
    padding: 50px;
    
}
.message-number{
    position: absolute;
    top:50px;
    margin-left: -40px;
    
}
.MainScroll span{
    
    color: var(--primaryBright);
    
}



.MainScrollTest {
    display: block;
    width:100%;
    white-space: nowrap;
    overflow: hidden;
}
.MainScrollTest span {
    display: inline-block;
    padding-left: 100%;
    -webkit-animate:scroll 10s infinite;
  
}


   


